@import url(https://fonts.googleapis.com/css?family=Bree+Serif|Titillium+Web);
body {
  background: url("/Images/movielarge_wp.jpg") no-repeat center border-box fixed;
  background-size: cover;
  padding-bottom: 60px;
}

#main {
  font-family: 'Titillium Web', sans-serif;
  /*

button#mmPlay
span#mmCurrentTime
span#mmTotalTime
input#mmProgress
button#mmMute
input#mmVolume
button#mmFull
*/
}

#main div.row {
  background: url("/Images/blackframe.jpg") repeat-x border-box;
  background-position: 0 0;
  background-size: contain;
  padding-top: 30px;
  height: auto;
}

#main #historywrapper .navbar.navbar-default {
  margin-bottom: 0;
  margin-left: -15px;
  margin-right: -15px;
  height: 70px;
  background-image: none;
  background: -moz-linear-gradient(top, rgba(36, 161, 151, 0.47) 0%, rgba(42, 160, 152, 0.47) 14%, rgba(46, 160, 153, 0.47) 24%, #429e9b 69%, #4f9d9d 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, rgba(36, 161, 151, 0.47) 0%, rgba(42, 160, 152, 0.47) 14%, rgba(46, 160, 153, 0.47) 24%, #429e9b 69%, #4f9d9d 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, rgba(36, 161, 151, 0.47) 0%, rgba(42, 160, 152, 0.47) 14%, rgba(46, 160, 153, 0.47) 24%, #429e9b 69%, #4f9d9d 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#7824a197', endColorstr='#4f9d9d',GradientType=0 );
  /* IE6-9 */
}

#main #historywrapper .navbar-brand {
  margin-left: 10px;
}

@media all and (max-width: 768px) {
  #main #historywrapper .navbar-header {
    width: 30%;
  }
}

@media all and (max-width: 768px) {
  #main #historywrapper div.nav.navbar-nav.navbar-right {
    width: 20%;
    position: absolute;
    right: 110px;
    top: -5px;
  }
}

@media all and (max-width: 768px) and (max-width: 600px) {
  #main #historywrapper div.nav.navbar-nav.navbar-right {
    right: 130px;
  }
}

#main #historywrapper ul.dropdown-menu {
  background-color: #333333;
  border-color: #333333;
  font-family: 'Bree Serif', serif;
  font-size: 1.1em;
  font-weight: 600;
}

#main #historywrapper ul.dropdown-menu > li:first-child {
  color: #dadada;
}

#main #historywrapper ul.dropdown-menu > li:first-child > span {
  color: red;
}

#main #historywrapper ul.dropdown-menu > li {
  padding-left: 15px;
}

#main #historywrapper ul.dropdown-menu > li > span {
  font-size: 0.9em;
}

#main #historywrapper ul.dropdown-menu > li.btn {
  margin: 3px 125px;
}

@media all and (max-width: 768px) {
  #main #historywrapper ul.dropdown-menu {
    position: absolute;
    width: 350px;
    top: 65px;
    left: -156px;
  }
}

#main #historywrapper ul.dropdown-menu #dropHistoryContent {
  height: 500px;
  width: 346px;
  overflow-y: auto;
  background: #dadada;
}

#main #historywrapper ul.dropdown-menu #dropHistoryContent span.droptitle {
  font-variant: small-caps;
  font-size: 1.15em;
  padding-left: 10px;
  display: block;
}

#main #historywrapper ul.dropdown-menu #dropHistoryContent span.droptime, #main #historywrapper ul.dropdown-menu #dropHistoryContent span.dropstatus {
  font-size: 0.75em;
  padding-left: 5px;
  display: inline-block;
}

#main #historywrapper ul.dropdown-menu #dropHistoryContent span.btn.btn-link {
  color: red;
  display: inline;
  margin-left: 20px;
}

#main #historywrapper .navbar-text {
  color: #333333;
  padding-left: 15px;
  padding-top: 5px;
  font-size: 1.5em;
}

@media all and (max-width: 768px) {
  #main #historywrapper .navbar-text {
    display: none;
  }
}

#main #historywrapper .dropdown > button {
  margin: 15px 20px;
}

#main #historywrapper .dropdown > button:active, #main #historywrapper .dropdown > button:focus {
  -webkit-box-shadow: 0px 0px 8px 4px rgba(158, 63, 158, 0.67);
  -moz-box-shadow: 0px 0px 8px 4px rgba(158, 63, 158, 0.67);
  box-shadow: 0px 0px 8px 4px rgba(158, 63, 158, 0.67);
  outline: 0;
}

#main #MovieModal {
  font-family: 'Bree Serif', serif;
}

#main #MovieModal #MovieModalLabel {
  font-family: 'Titillium Web', sans-serif;
  font-size: 1.75em;
  font-variant: small-caps;
  color: #f3f3f3;
}

#main #MovieModal .modal-body p {
  font-size: 1.1em;
  font-weight: 700;
  margin-bottom: 0.3em;
}

@media all and (max-width: 600px) {
  #main #MovieModal .modal-body p {
    font-size: 0.8em;
  }
}

