  font-size: 1.25em;
  font-weight: 300;
}

@media all and (max-width: 600px) {
  #main #MovieModal .modal-body p > span {
    font-size: 0.95em;
  }
}

#main #MovieModal .modal-header, #main #MovieModal .modal-footer {
  background-color: #333333;
}

#main #MovieModal .modal-header > button > span {
  background-color: #f3f3f3;
}

#main #MovieModal #mmImage img {
  height: 8em;
}

#main #mmPlayer {
  position: relative;
}

#main #mmPlayer:hover #mmVideo-controls, #main #mmPlayer:focus #mmVideo-controls {
  opacity: 0.9;
}

#main #mmVideo-controls {
  padding: 5px;
  padding-left: 10px;
  width: 96%;
  position: absolute;
  bottom: 5px;
  left: 0;
  right: 0;
  font-size: 0.9em;
  color: #dfdfdf;
  opacity: 0.5;
  height: auto;
  -webkit-transition: opacity .3s;
  -moz-transition: opacity .3s;
  -o-transition: opacity .3s;
  -ms-transition: opacity .3s;
  transition: opacity .3s;
  background-image: none;
  background: -moz-linear-gradient(top, rgba(36, 161, 151, 0.47) 0%, rgba(42, 160, 152, 0.47) 14%, rgba(46, 160, 153, 0.47) 24%, #429e9b 69%, #4f9d9d 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, rgba(36, 161, 151, 0.47) 0%, rgba(42, 160, 152, 0.47) 14%, rgba(46, 160, 153, 0.47) 24%, #429e9b 69%, #4f9d9d 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, rgba(36, 161, 151, 0.47) 0%, rgba(42, 160, 152, 0.47) 14%, rgba(46, 160, 153, 0.47) 24%, #429e9b 69%, #4f9d9d 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#7824a197', endColorstr='#4f9d9d',GradientType=0 );
  /* IE6-9 */
}

@media all and (max-width: 660px) {
  #main #mmVideo-controls {
    font-size: 0.7em;
    width: 93%;
  }
}

#main #mmVideo-controls > button, #main #mmVideo-controls input, #main #mmVideo-controls span {
  display: inline-block;
  padding: 2px 2px;
  margin-right: 10px;
}

#main #mmVideo-controls > button:active, #main #mmVideo-controls > button:focus, #main #mmVideo-controls input:active, #main #mmVideo-controls input:focus, #main #mmVideo-controls span:active, #main #mmVideo-controls span:focus {
  outline-color: #fff;
}

#main #mmVideo-controls > button:hover, #main #mmVideo-controls input:hover, #main #mmVideo-controls span:hover {
  cursor: pointer;
}

#main #mmVideo-controls > button {
  height: 20px;
  width: 20px;
  background: rgba(0, 0, 0, 0.5);
  border: 0;
  color: #EEE;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -o-border-radius: 3px;
  border-radius: 3px;
}

@media all and (max-width: 660px) {
  #main #mmVideo-controls > button {
    height: 12px;
    width: 12px;
    padding: 0;
    margin-right: 5px;
  }
}

#main #mmVideo-controls > input[type=range]::-webkit-slider-thumb {
  transform: rotate(90deg);
}

#main #mmVideo-controls > input#mmProgress {
  width: 40%;
  height: 15px;
}

@media all and (max-width: 660px) {
  #main #mmVideo-controls > input#mmProgress {
    width: 30%;
    height: 8px;
    margin-right: 5px;
  }
}

#main #mmVideo-controls > input#mmVolume {
  width: 15%;
  height: 15px;
}

@media all and (max-width: 660px) {
  #main #mmVideo-controls > input#mmVolume {
    width: 8%;
    height: 8px;
    margin-right: 5px;
  }
}

#main #Content {
  padding-top: 40px;
  font-family: 'Bree Serif', serif;
  font-size: 2em;
  color: #043d32;
}

#main footer {
  background-image: none;
  background-color: #333333;
  font-family: 'Bree Serif', serif;
  font-size: 1.2em;
  color: #fff;
  text-align: center;
}

#main footer p:first-child {
  margin-bottom: -15px;
  padding-top: 10px;
  font-size: 1.4em;
}

@media all and (max-width: 768px) {
  #main footer p:first-child {
    margin-bottom: 0;
  }
}

#main footer p a {
  color: inherit;
  text-decoration: none;
  display: inline-block;
}




